import React, { useState } from "react";
import Nav from "../common/nav";
import Footer from "../common/footer";
import Card from "../common/card";
import _ from "lodash";

const Home = (params) => {
  const [envelopes, setEnvelopes] = useState([
    {
      id: 1,
      name: "Emergency Savings",
      account: "HY Savings",
      desc: "Buffer fund",
      tag: { name: "Savings", color: "warning" },
      values: {
        goal: 12687,
        amount: 10724.73,
      },
    },
    {
      id: 2,
      name: "Dining Out",
      account: "General Spending",
      desc: "Money for eating out",
      tag: { name: "Food", color: "success" },
      values: {
        goal: null,
        amount: 120,
      },
    },
    {
      id: 3,
      name: "Groceries",
      account: "General Spending",
      desc: "Money for grocery spending",
      tag: { name: "Food", color: "success" },
      values: {
        goal: null,
        amount: 280.19,
      },
    },
    {
      id: 4,
      name: "Kids",
      account: "General Spending",
      desc: "Kid related purchases",
      tag: { name: "Kids", color: "info" },
      values: {
        goal: null,
        amount: 30,
      },
    },
  ]);

  const renderEnvelopes = () => {
    return Object.values(envelopes).map((values) => <Card props={values} />);
  };

  return (
    <>
      <Nav />
      <section>
        <div className="container">
          <h1>Envelopes</h1>
          <hr />
          <div className="flex-wrapper">
            {!_.isEmpty(envelopes) && renderEnvelopes()}
          </div>
        </div>
      </section>
      <Footer />
    </>
  );
};

export default Home;
